@import '../../common/mixins';
@import '../../common/vars';

/**
 * HEADER
 */

.header {
	padding: 28px 0;
	&-logo {
		position: relative;
		justify-content: left;
		display: flex;
		&::after {
			height: 100%;
			position: absolute;
			content: "";
			border: 2px solid #a98f71;
			border-radius: 15px;
			right: 0 {
		    top: 0; } }
		a {
			display: flex;
			align-items: center;
			text-decoration: none;
			&:hover {
				opacity: 0.5; } }
		&_image {
			width: 50px;
			img {
				width: 100%; } }
		&_text {
			font-weight: 400;
			font-size: 30px;
			font-family: "Acrom";
			color: rgb(40, 40, 40);
			text-transform: uppercase;
			max-width: 200px;
			margin-left: 10px;
			line-height: 31px; } }
	&-heading {
		line-height: 33px;
		&_title {
			font-weight: 500;
			font-size: 26px;
			font-family: "Open Sans";
			color: rgb(40, 40, 40);
			font-style: italic; }
		&_subtitle {
			font-weight: 500;
			font-size: 20px;
			font-family: "Open Sans";
			color: rgb(181, 147, 88);
			font-style: italic; } }
	&-icons {
		margin: auto;
		max-width: 140px;
		display: flex;
		align-items: center;
		justify-content: space-between;
		&_facebook {
			position: relative;
			a {
				&::before {
					cursor: pointer;
					display: none;
					top: 3px;
					position: absolute;
					content: url(../img/icons/facebook2.png); }
				&:hover {
					&::before {
						display: block; } } } }
		&_insta {
			&:hover {
				opacity: 0.5; } }
		&_language {
			margin-top: -5px; } }
	.im {
		display: none; } }

@media (max-width: 980px) {
	.header {
		&-logo {
			&::after {
				display: none;
				right: -14px; }
			&_text {
				font-size: 25px; } } } }

@media (max-width: 850px) {
	.header {
		&-logo {
			position: relative;
			justify-content: center;
			display: flex;
			&::after {
				right: -14px; } } } }

@media (max-width: 770px) {
	.header {
		.im {
			display: block; }
		&-iconsmob {
			margin: auto;
			max-width: 140px;
			display: flex;
			align-items: center;
			justify-content: space-between;
			&_facebook {
				position: relative;
				a {
					&::before {
						cursor: pointer;
						display: none;
						top: 2px;
						position: absolute;
						content: url(../img/icons/facebook2.png); }
					&:hover {
						&::before {
							display: block; } } } }
			&_insta {
				&:hover {
					opacity: 0.5; } }
			&_language {
				margin-top: -5px; } }

		&-logo {
			margin-top: 30px;
			margin-bottom: 30px;
			&::after {
				display: none; } }
		&-heading {
			text-align: center;
			margin-bottom: 30px; }
		&-icons {
			display: none; } } }
