@import '../../common/mixins';
@import '../../common/vars';

/**
 * TILE
 */

.tile {
    margin-bottom: 80px;
    &-item {
        margin-bottom: 20px;
        position: relative;
        &::before {
            content: '';
            width: 95%;
            height: 95%;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
            border: 2px solid #fff; }
        &_image {
            background-image: url(../img/tile/1.png);
            padding-top: 80%;
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center center; }
        &_title {
            margin: 0 auto;
            margin-top: -50px;
            margin-bottom: 10px;
            max-width: 200px;
            text-align: center;
            background-color: #c1ae97;
            padding: 5px;
            position: relative;
            &_txt {
                font-size: 18px;
                font-family: "Open Sans";
                color: rgb(255, 255, 255);
                text-transform: uppercase;
                border: 1px solid #ffffff; } }
        &_subtitle {
            text-align: center;
            font-size: 14px;
            font-family: "Open Sans";
            color: rgb(40, 40, 40);
            font-style: italic;
            margin-bottom: 10px; }
        &_imagebottom {
            background-image: url(../img/tile/tilebottom.png);
            padding-top: 15%;
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center center;
            margin-top: -60px; } } }
