@import '../../common/mixins';
@import '../../common/vars';

/**
 * CITIES
 */

.cities {
    .br {
        padding-bottom: 30px;
        border-bottom: 4px solid #d6c9b9;
        margin-bottom: 55px; }
    ul {
        li {
            span {
                font-weight: 600;
                font-family: "Open Sans"; }
            a {
                font-size: 13px;
                font-family: "Open Sans";
                text-decoration: none;
                color: black;
                &:hover {
                    color: #806e59; } } } } }

@media (max-width: 770px) {
    .cities {
        display: none; } }
