@import '../../common/mixins';
@import '../../common/vars';

/**
 * FOOTER
 */

footer {
    padding-bottom: 30px;
    .foot {
        margin-bottom: 30px;
        align-items: center; }
    .mb {
        margin-bottom: 5px; }
    span {
        color: rgb(164, 136, 104); }
    a {
        font-size: 14px;
        font-family: "Acrom";
        color: rgb(164, 136, 104);
        font-weight: bold;
        text-transform: uppercase;
        &:hover {
            text-decoration: none; } }
    input {
        border-radius: 50px;
        padding: 5px 0 5px 15px;
        border: 2px solid #a48868;
        font-size: 16px;
        font-family: "Acrom";
        color: rgb(209, 195, 179);
        text-transform: uppercase;
        outline: none; }
    .iconsmob {
        display: flex;
        align-items: center;
        justify-content: space-between; } }

@media (max-width: 940px) {
    footer {
        .foot {
            text-align: center; }
        .iconsmob {
            display: flex;
            align-items: center;
            justify-content: space-evenly; }
        span {
            color: rgb(164, 136, 104);
            font-size: 13px; } } }
