@import '../../common/mixins';
@import '../../common/vars';

/**
 * GREETING
 */

.greeting {
    margin-bottom: 60px;
    &-left {
        max-width: 670px;
        &_title {
            font-size: 30px;
            font-family: "Acrom";
            color: rgb(40, 40, 40);
            font-weight: bold;
            text-transform: uppercase; }
        &_subtitle {
            font-size: 30px;
            font-family: "Acrom";
            color: rgb(40, 40, 40);
            font-weight: normal;
            text-transform: uppercase;
            margin-bottom: 20px; }
        &_text {
            text-align: justify;
            p {
                font-family: "Open Sans"; } } }
    &-right {
        max-width: 335px;
        background-color: #f6f2ef;
        border: 10px solid #ffffff;
        padding: 25px 25px;
        margin-top: -155px;
        text-align: center;
        position: relative;
        &_image {
            width: 200px;
            height: 200px;
            border: 10px solid #ffffff;
            border-radius: 50%;
            overflow: hidden;
            background-image: url(../img/icons/greetingbg.png);
            background-repeat: no-repeat;
            background-size: cover;
            margin: 0 auto;
            margin-top: -85px;
            margin-bottom: 25px;
            background-position-y: -1px; }
        &_title {
            position: relative;
            font-size: 25px;
            font-family: "Acrom";
            color: rgb(115, 115, 111);
            font-weight: bold;
            text-transform: uppercase;
            z-index: 1; }
        &_subtitle {
            position: relative;
            font-size: 16px;
            font-family: "Acrom";
            color: rgb(152, 152, 145);
            font-weight: bold;
            text-transform: uppercase;
            z-index: 1; }
        &_about {
            position: relative;
            font-size: 16px;
            font-family: "Acrom";
            color: rgb(152, 152, 145);
            font-weight: bold;
            text-transform: uppercase;
            margin-bottom: 20px;
            z-index: 1; }
        &_line {
            background-color: #fff;
            width: 55px;
            height: 10px;
            border-radius: 10px;
            margin: 0 auto;
            margin-bottom: 25px;
            position: relative;
            z-index: 1; }
        &_line1 {
            position: relative;
            &::after {
                content: url("../img/icons/list.png");
                position: absolute;
                left: -107px;
                top: -176px; }
            &::before {
                content: url("../img/icons/list2.png");
                position: absolute;
                left: 163px;
                top: -205px; } }
        &_line2 {
            position: relative;
            &::after {
                content: url("../img/icons/list4.png");
                position: absolute;
                left: -43px;
                top: 28px; }
            &::before {
                content: url("../img/icons/list3.png");
                position: absolute;
                left: 102px;
                top: -24px; } }

        &_text {
            position: relative;
            font-size: 14px;
            font-family: "Open Sans";
            color: rgb(40, 40, 40);
            font-style: italic;
            font-weight: 600; } } }

@media (max-width: 768px) {
    .greeting {
        &-left {
            &_title {
                line-height: 0.5; }
            &_text {
                p {
                    font-size: 12px; } } }
        &-neset {
            flex-direction: column-reverse; }
        &-right {
            margin: 0 auto;
            margin-bottom: 35px;
            margin-top: -40px; } } }
