@import '../../common/mixins';
@import '../../common/vars';

/**
 * BG
 */

.bg {
	background-position: center center;
	background-size: cover;
	min-height: 400px;
	background-repeat: no-repeat;
	background-image: url(../img/bg/bg.png);
	margin-bottom: 40px;
	&-weather {
		align-items: center;
		display: flex;
		justify-content: space-between;
		background-color: rgba(255, 255, 255, 0.301);
		border-radius: 50px;
		padding: 0px 30px;
		margin-top: 30px;
		max-width: 280px;
		margin-left: auto;
		&_icon {
			width: 40px;
			img {
				width: 100%; } }
		&_city {
			color: #ffffff;
			font-size: 18px; }
		&_value {
			color: #ffffff;
			font-size: 40px; } } }

@media (max-width: 950px) {
	.bg {
		min-height: 250px;
		&-weather {
			margin: 30px auto;
			margin-top: 31px;
			width: 250px; } } }
