@import "vars";
@import "mixins";

/**
 * GENERAL
 */

* {
	margin: 0;
	padding: 0;
	color: #222;
	box-sizing: border-box; }

html,
body {
	height: 100%;
	font-family: GothamPro;
	font-weight: 300; }

body {
	// background: url(../img/bg.png) no-repeat
	background-position: top right;
	background-size: auto;
	background-color: #ffffff; }


.wrapper {
	display: flex;
	flex-direction: column;
	min-height: 100%; }

.content {
	flex: 1 0 auto;
	width: 100%; }

li {
	list-style-type: none; }

a {
	text-decoration: none;
	&:hover {
		transition: 0.3s; } }

h2 {
	font-size: 30px; }

p {
	font-family: "Open Sans";
	font-size: 14px;
	color: rgb(40, 40, 40);
	line-height: 1.714;
	font-weight: 600; }


