.header {
  padding: 28px 0;
}

.header-logo {
  position: relative;
  justify-content: left;
  display: flex;
}

.header-logo::after {
  height: 100%;
  position: absolute;
  content: "";
  border: 2px solid #a98f71;
  border-radius: 15px;
  right: 0;
  right-top: 0;
}

.header-logo a {
  display: flex;
  align-items: center;
  text-decoration: none;
}

.header-logo a:hover {
  opacity: 0.5;
}

.header-logo_image {
