}

.header-heading_subtitle {
  font-weight: 500;
  font-size: 20px;
  font-family: "Open Sans";
  color: #b59358;
  font-style: italic;
}

.header-icons {
  margin: auto;
  max-width: 140px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header-icons_facebook {
  position: relative;
}

.header-icons_facebook a::before {
  cursor: pointer;
  display: none;
  top: 3px;
  position: absolute;
  content: url(../img/icons/facebook2.png);
}

.header-icons_facebook a:hover::before {
  display: block;
}

.header-icons_insta:hover {
  opacity: 0.5;
}

.header-icons_language {
