}

@media (max-width: 850px) {
  .header-logo {
    position: relative;
    justify-content: center;
    display: flex;
  }
  .header-logo::after {
    right: -14px;
  }
}

@media (max-width: 770px) {
  .header .im {
    display: block;
  }
  .header-iconsmob {
    margin: auto;
    max-width: 140px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .header-iconsmob_facebook {
    position: relative;
  }
  .header-iconsmob_facebook a::before {
    cursor: pointer;
    display: none;
    top: 2px;
    position: absolute;
    content: url(../img/icons/facebook2.png);
  }
  .header-iconsmob_facebook a:hover::before {
    display: block;
  }
  .header-iconsmob_insta:hover {
    opacity: 0.5;
  }
  .header-iconsmob_language {
    margin-top: -5px;
  }
  .header-logo {
    margin-top: 30px;
    margin-bottom: 30px;
  }
  .header-logo::after {
    display: none;
  }
  .header-heading {
    text-align: center;
