  margin-bottom: 55px;
}

.cities ul li span {
  font-weight: 600;
  font-family: "Open Sans";
}

.cities ul li a {
  font-size: 13px;
  font-family: "Open Sans";
  text-decoration: none;
  color: black;
}

.cities ul li a:hover {
  color: #806e59;
}

@media (max-width: 770px) {
  .cities {
    display: none;
  }
}

/**
 * BLOG
 */
.blog-title {
  font-size: 22px;
  font-family: "Acrom";
  font-weight: 500;
}

.blog-image img {
  width: 100%;
}

/**
 * COMMENT
 */
.comment {
  margin-bottom: 40px;
}

.comment-title {
