@import '../../common/mixins';
@import '../../common/vars';

/**
 * NEWS */

.news {
    margin-bottom: 20px;
    .bord {
        border-bottom: 4px solid #d6c9b9;
        margin-bottom: 20px; }
    &-items {
        border: 2px solid #d6c9b9;
        padding: 50px 40px 0 40px;
        .col-md-5 {
            padding: 0;
            padding-right: 15px; }
        .col-md-7 {
            padding: 0; }
        .col-md-12 {
            padding: 0; }
        &_lastnews {
            display: flex;
            align-items: center;
            margin-top: -85px;
            margin-bottom: 10px;
            &_image {
                background-color: #c1ae97;
                max-width: 70px;
                img {
                    width: 100%;
                    padding: 2px 8px; } }
            &_text {
                padding: 10px;
                font-size: 12px;
                font-family: "Acrom";
                color: rgb(255, 255, 255);
                font-weight: bold;
                text-transform: uppercase;
                background-color: #d6c9b9; } }
        &_item {
            display: flex;
            justify-content: center;
            &_image {
                margin-bottom: 20px;
                img {
                    width: 100%; } }
            &_title {
                font-size: 15px;
                font-family: "Acrom";
                color: rgb(40, 40, 40);
                font-weight: bold;
                line-height: 1.143;
                text-transform: uppercase;
                margin-bottom: 15px; }
            &_subtitle {
                font-size: 12px;
                font-family: "Open Sans";
                color: rgb(40, 40, 40);
                line-height: 1.143;
                margin-bottom: 15px;
                font-weight: 600; }
            &_more {
                margin-bottom: 25px;
                a {
                    border: 2px solid #a48868;
                    border-radius: 50px;
                    text-decoration: none;
                    padding: 2px 15px;
                    text-transform: uppercase;
                    font-size: 12px;
                    font-family: "Open Sans";
                    color: rgb(128, 110, 89);
                    font-weight: 400;
                    &:hover {
                        background-color: #f0eae4;
                        transition: 0.5s; } } }
            &_icons {
                display: flex;
                margin-bottom: 5px;
                &_icon {
                    display: flex;
                    color: #767676;
                    margin-right: 10px;
                    img {
                        width: 15px;
                        margin-right: 3px; } } } }
        &_ad {
            min-height: 300px;
            background-color: #f6f2ef;
            margin-bottom: 30px; }
        &_moreb {
            display: none;
            text-align: center;
            margin-bottom: 10px;
            a {
                border: 2px solid #a48868;
                border-radius: 50px;
                text-decoration: none;
                padding: 2px 15px;
                text-transform: uppercase;
                font-size: 12px;
                font-family: "Open Sans";
                color: rgb(128, 110, 89);
                font-weight: 400;
                &:hover {
                    background-color: #f0eae4;
                    transition: 0.5s; } } }
        &_big {
            width: 100%;
            min-height: 300px;
            background-color: #f6f2ef;
            margin-bottom: 10px; }
        &_small {
            width: 100%;
            min-height: 200px;
            background-color: #f6f2ef;
            margin-bottom: 10px; }
        &_form {
            width: 100%;
            min-height: 395px;
            background-color: #f6f2ef;
            margin-bottom: 10px; } } }

@media (max-width: 1050px) {
    .news {
        &-items {
            &_item {
                &_icons {
                    &_icon {
                        span {
                            font-size: 11px; } } } } } } }

@media (max-width: 767px) {
    .news {
        &-items {
            .col-md-5 {
                padding-right: 0; }
            &_item {
                &_icons {
                    &_icon {
                        span {
                            font-size: 9px; } } } }
            &_moreb {
                display: block; }
            &_big {
                display: none; }
            &_small {
                display: none; }
            &_form {
                display: none; } } } }
