@import '../../common/mixins';
@import '../../common/vars';

/**
 * LISTING
 */

.listing {
    margin-bottom: 80px;
    &-items {
        padding: 0 60px; }
    &-item {
        text-align: center;
        &_image {
            background-image: url(../img/carousel/1.png);
            padding-top: 64%;
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center center;
            border: 10px solid #f6f2ef;
            margin-bottom: 10px;
            img {
                display: none !important; } }
        &_title {
            font-size: 16.755px;
            font-family: "Acrom";
            color: rgb(115, 115, 111);
            font-weight: bold;
            text-transform: uppercase;
            margin-bottom: 10px; }
        &_text {
            margin-bottom: 15px;
            p {
                font-size: 13px;
                font-family: "Open Sans";
                color: rgb(40, 40, 40);
                margin: 0; } }
        &_readmore {
            margin-bottom: 1px;
            a {
                border: 2px solid #a48868;
                border-radius: 50px;
                text-decoration: none;
                padding: 2px 15px;
                text-transform: uppercase;
                font-size: 12px;
                font-family: "Open Sans";
                color: rgb(128, 110, 89);
                font-weight: 400;
                &:hover {
                    background-color: #f0eae4;
                    transition: 0.5s; } } } } }
